import React from 'react'
import DbLayout from '../../components/dblayout'
import Cookies from 'js-cookie'
import {navigate} from 'gatsby'
import {Link} from 'gatsby-plugin-intl'

export default () => {
    const handleLogout = () => {
        Cookies.remove('user');
        navigate('/login/');
    }
    return (
        <DbLayout>
            <div className="px-2 py-8 lg:px-4 lg:py-8 bg-white shadow text-center">
                <div className="text-18 font-semibold text-lights-bluebold mb-4">Do you really want to logout?</div>
                <div className="mb-4">
                    <Link to="/db/dashboard/" className="text-lights-orangebold font-sans font-semibold hover:underline">To go back to my account</Link>
                </div>
                <div className="">
                    <button className="px-8 py-4 bg-lights-orangebold text-white text-18 font-sans font-bold rounded-full focus:outline-none" onClick={handleLogout}>Logout</button>
                </div>
            </div>            
        </DbLayout>
    )
}